import {Component, OnInit} from '@angular/core';
import {DovizService} from '../../services/doviz.service';
import {DovizModel} from '../../models/DovizModel';
import {HttpErrorResponse} from '@angular/common/http';
import {MatSnackBar} from '@angular/material';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  private dataSource: DovizModel[];
  private displayedColumns: string[] = [ 'name', 'buyPrice', 'sellPrice'];
  private current_type = 0;
  private showProgress: boolean;

  constructor(public service: DovizService, private snack: MatSnackBar) {

  }

  ngOnInit(): void {
  }

  getDoviz(type: number) {
    this.showProgress = true;
    this.current_type = type;
    this.service.getDoviz(this.current_type).subscribe(data => {
      console.log(data);
      this.dataSource = data;
    }, error => {
      const res: HttpErrorResponse = error;
      this.showNack(res.message);
      this.showProgress = false;
    }, () => {
      console.log('complete');
      this.showProgress = false;
    });
  }

  getFormattedNumber(value: number): string {
    if (this.current_type === this.service.TYPE_DOVIZ) {
      return Number(value).toFixed(4);
    } else if (this.current_type === this.service.TYPE_KRIPTO) {
      return Number(value).toFixed(2);
    } else if (this.current_type === this.service.TYPE_ALTIN) {
      return Number(value).toFixed(2);
    }
  }

  private showNack(msj: string): void {
    this.snack.open(msj, 'Tamam', {
      duration: 3000,
    });
  }
}
