import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {DovizModel} from '../models/DovizModel';


@Injectable({
  providedIn: 'root'
})
export class DovizService {

  private URL = 'https://api.canlidoviz.com/web/items?marketId=1&type=';
  TYPE_DOVIZ = 0;
  TYPE_ALTIN = 1;
  TYPE_KRIPTO = 2;

  constructor(private http: HttpClient) {

  }

  getDoviz(type: number): Observable<DovizModel[]> {
    return this.http.get<DovizModel[]>(this.URL + type);
  }

}
